import React from "react";
import { Container, Row, } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import images from "./google-icon.svg";
import "./style.css";
import BoxComponent from "./BoxComponent";




const MyComponent = () => {
  const boxData = [
    { name: "Previous MCQ", imageUrl: images },
    { name: "Subject_wise MCQ", imageUrl: images },
    { name: "Non_nursing MCQ", imageUrl: images },
    { name: "Model_mock Test", imageUrl: images },
    { name: "Hybrid Test", imageUrl: images },
    { name: "Discussion Forum", imageUrl: images },
  ];

  return (
    <Container>
      <Row className="text-center mt-5 text-row">
        <div>
          <h4>Category</h4>
        </div>
      </Row>
      <Row className="d-flex justify-content-center lg-p-5 ">
        {boxData.map((box, index) => (
          <BoxComponent key={index} {...box} />
        ))}
      </Row>
    </Container>
  );
};

export default MyComponent;
