// Navbar.jsx
import React, { useState } from "react";
import AppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import IconButton from "@mui/material/IconButton";
import NotificationsIcon from "@mui/icons-material/Notifications";
import AccountCircleIcon from "@mui/icons-material/AccountCircle";
import MenuIcon from "@mui/icons-material/Menu";
import Drawer from "@mui/material/Drawer";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemText from "@mui/material/ListItemText";
import Hidden from "@mui/material/Hidden";
import styles from "./style";
import image from './google-icon.svg';

const Navbar = () => {
  const [isDrawerOpen, setIsDrawerOpen] = useState(false);

  const toggleDrawer = () => {
    setIsDrawerOpen(!isDrawerOpen);
  };

  return (
    <AppBar position="static" style={styles.appBar}>
      <Toolbar style={styles.toolbar}>
        <div style={styles.logoContainer}>
          <Hidden lgUp mdUp smUp>
            <IconButton color="inherit" onClick={toggleDrawer} style={styles.menuIcon}>
              <MenuIcon />
            </IconButton>
          </Hidden>
          <img src={image} alt="Company Logo" style={{ marginRight: "10px" }} height={20} width={20} />
          <Typography variant="h6" component="div" style={{ color: "black" }}>
            Company Name
          </Typography>
        </div>

        <Hidden smDown>
          <div style={{ display: "flex", justifyContent: "space-evenly", flexGrow: 1 }}>
            <Typography style={styles.menuText}>Home</Typography>
            <Typography style={styles.menuText}>Courses</Typography>
            <Typography style={styles.menuText}>Blog</Typography>
            <Typography style={styles.menuText}>About Us</Typography>
            <Typography style={styles.menuText}>Contact Us</Typography>
          </div>
        </Hidden>

        <div />

        <Hidden smDown>
          <IconButton color="black">
            <NotificationsIcon />
          </IconButton>

          <IconButton color="black">
            <AccountCircleIcon />
          </IconButton>
        </Hidden>

        {/* Mobile Drawer */}
        <Hidden lgUp mdUp smUp>
          <Drawer anchor="right" open={isDrawerOpen} onClose={toggleDrawer}>
            <List>
              {["Home", "Courses", "Blog", "About Us", "Contact Us"].map((text) => (
                <ListItem button key={text} onClick={toggleDrawer}>
                  <ListItemText primary={text} />
                </ListItem>
              ))}
            </List>
          </Drawer>
        </Hidden>
      </Toolbar>
    </AppBar>
  );
};

export default Navbar;
